body{
  font-size: 20px;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  color: white;
}
header{
  display: grid;
  grid-template-columns: 1fr 3fr 1fr 1fr;
  margin: 0px;
  align-items: center;
  text-align: center;
  font-size: 30px;
  background-color: rgba(0, 0, 0, 0.296);
}
input {
  font-size: 30px; 
  background-color: rgba(255, 255, 255, 0.659);
  border-radius: 15px;
}
.nav{
  text-decoration: none;
  color: white;
  height: 100%;
  display: grid;
  align-items: center;
  transition: .25s ease-in-out;
}
.nav:hover{
  background-color: rgba(0, 0, 0, 0.495);
}

article{
  width: 95vw;
  margin: 0 auto;
}
.TakeMeButton{
  text-decoration: none;
  color: white;
  background-color: rgba(0, 0, 0, 0.296);
  padding: 15px;
  border-radius: 10px;
  border: 1px solid black;
  transition: .25s ease-in-out;
  box-shadow: 0px 0px 4px black;
}
.TakeMeButton:hover{
  background-color: rgba(0, 0, 0, 0.495);
}
.sectionTitle{
  text-align: center;
}
.Daily{
  column-gap: 20px;
  overflow: auto;
}
.DailyDay{
  width: 250px;
  text-align: center;
}
.Day{
  justify-content: center;
  justify-items: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "sidebar content" "header header";
}
.grid-item-3{
  grid-column: span 2;
  width: 100%;
  margin: 0px;
  text-align: center;
}
.overflow{
  background-color: rgba(0, 0, 0, 0.296);
  border-radius: 20px 20px 0 0;
  display: flex;
  flex-flow: row;
}
.recommendation{
  margin: 50px 0 50px 0;
}
.hourly{
  transition: .25s ease-in-out;
  height: 0px;
  column-gap: 20px;
  overflow: auto;
}
.viewButton{
  border-top: black solid 1px;
  border-bottom: black solid 1px;
  background-color: rgba(0, 0, 0, 0.296);
  user-select: none;
}
.viewButton:hover{
  background-color: rgba(0, 0, 0, 0.495);
}
.News{
  column-gap: 25px;
  overflow: auto;
  margin-bottom: 50px;
}
.new{
  text-decoration: none;
  color: white;
  text-align: center;
  border: 1px solid white;
  border-radius: 0 0 20px 20px;
  transition: 0.25s ease-in-out;
}
.new:hover{
  background-color: rgba(0, 0, 0, 0.495);
}
.SplashPageInfo{
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: center;
  column-gap: 50px;
  width: 90%;
  margin: 0 auto;
  margin-top: 50px;
}
.SplashTitle{
  font-size: 50px;
  margin: 25px;
}
.SplashDesc{
  font-size: 25px;
  margin: 20vh 0px 20vh 0px;
}
.AboutSlideshow{
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: center;
  row-gap: 30vh;
  margin-top: 25vh;
  margin-bottom: 25vh;
}
.AboutSlideshowText{
  width: 90%;
  padding: 2%;
  background-color: rgba(0, 0, 0, 0.296);
}
article{
  margin-top: 10vh;
  margin-bottom: 10vh;
}
/* width */
::-webkit-scrollbar {
  width: 20%;
  height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: white; 
  border-radius: 6px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

footer{
  text-align: center;
  background-color: rgba(0, 0, 0, 0.495);
  padding-top: 5vh;
  padding-bottom: 5vh;
}

.searchResults{
  display: grid;
  background-color: rgba(0, 0, 0, 0.296);
  margin-top: 20px;
  min-height: 150px;
  text-align: center;
  align-items: center;
}

.searchResult{
  color: white;
  text-decoration: none;
  padding: 15px 0 15px 0;
}
.searchResult:hover{
  background-color: rgba(0, 0, 0, 0.495);
}
.LocationPage{
  text-align: center;
}

@media (max-width:600px){
  .TakeMeButton{
    font-size: 20px;
  }
  .SplashImage{
    display: none;
  }
  .SplashPageInfo{
  grid-template-columns: 1fr;
  text-align: center;
  }
  .AboutSlideshow{
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    row-gap: 30vh;
    margin-top: 25vh;
    margin-bottom: 25vh;
  }
}